body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Home {
  display: grid;
  grid-template-columns: 100%;
  -webkit-justify-content: center;
          justify-content: center;
}
.scroll{
  background-color: white;
  height: 88vh;
  overflow-y: auto;
}
.Filter{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: stretch;
          align-items: stretch;
  margin-top: 5%;
}
.filtergroup{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.buttonfilter{
  margin: 1% auto;
}
.checkboxtree{
  margin-top: 5%;
}
.buttongroup{
  margin: auto;
}

.selectcontainer{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-content: space-around;
          align-content: space-around;
}

@media only screen and (max-width: 925px){
  .select-switch{
    display: grid;
    grid-template-columns: 40vw 5vw 40vw;
    grid-template-rows: 50% 50%;
    justify-items: center;
    -webkit-align-items: center;
            align-items: center;
    margin: 1%;
  }
  .select-axis{
    display: grid;
    grid-template-columns: 90vw;
    grid-template-rows: 50% 50%;
    justify-items: stretch;
    margin: 1%;
  }
}
@media only screen and (min-width: 925px){
  .select-switch{
    display: grid;
    grid-template-columns: 13vw 6vw 13vw;
    grid-template-rows: 50% 50%;
    justify-items: center;
    -webkit-align-items: center;
            align-items: center;
    margin: 1%;
  }
  .select-axis{
    display: grid;
    grid-template-columns: 30vw;
    grid-template-rows: 50% 50%;
    justify-items: stretch;
    margin: 1%;
  }
}
.select-switch-title{
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 2;
}
.select-switch-front{
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
}
.select-switch-center{
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 3;
}
.select-switch-back{
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 2;
  grid-row-end: 3;
}

.scroll-fix{
  height: 88vh;
  overflow-y: auto;
}
.buttongroup{
  width: 75%;
}

.Header{
  height: 12vh;
  overflow: hidden;
}
.header-logo{
  height: 5vh;
}
.Modal {
   position: absolute;
   top: 1%;
   left: 1%;
   right: 1%;
   bottom: 1%;
   background-color: #eee;
   z-index: 99999;
   overflow-y: auto;
 }

 .Overlay {
   position: fixed;
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;
   background-color: #111;
   z-index: 9999;
 }

